html {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.logo {
  align-self: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  position: relative;
}

.nav {
  text-align: center;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

nav ul li:not(:first-child) {
  margin-left: 20px;
}

.nav__link.is-active {
  color: var(--blue-color);
  height: 60px;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--blue-color);
}
